$pad:992px;
$mobile:767px;

@mixin pad {
    @media(max-width:$pad){
        @content;
    }
}
@mixin mobile {
    @media(max-width:$mobile){
        @content;
    }
}
.container{
    max-width: 1110px;
    margin: 0 auto;
    @include pad{
        max-width: 690px;
    }
    @include mobile{
        max-width: 345px;
    }
}