// Font Size
.text{
    &-sm{
        font-size: $text-sm;
    }
    &-base{
        font-size: $text-base;
    }
    &-lg{
        font-size: $text-lg;
    }
    &-xl{
        font-size: $text-xl;
    }
    &-2xl{
        font-size: $text-2xl;
    }
    &-3xl{
        font-size:$text-3xl;
    }
    &-4xl{
        font-size:$text-4xl;
    }
    &-5xl{
        font-size:$text-5xl;
    }
    &-6xl{
        font-size:$text-6xl;
    }
    &-7xl{
        font-size:$text-7xl;
    }

}
//text color
.text{
    &-primary{
        color: $primary;
    }
    &-secondary{
        color: $secondary;
    }
    &-light{
        color: $light;
    }
    &-dark{
        color: $dark;
    }
    &-white{
        color:$white ;
    }
    &-gray-100{
        color: $gray-100 ;
    }
    &-gray-200{
        color: $gray-200 ;
    }
    &-gray-300{
        color: $gray-300 ;
    }
    &-gray-400{
        color: $gray-400 ;
    }
    &-gray-500{
        color: $gray-500 ;
    }
    &-gray-600{
        color: $gray-600 ;
    }
    &-orange-600{
        color: $orange-600 ;
    }
    &-orange-800{
        color: $orange-800 ;
    }
}

//Link hover
.hoverLink-opacity-5:hover{
    opacity: 0.5;
}

//Bg Color
.bg{
    &-light{
        background: $light;
    }
    &-primary{
        background: $primary;
    }
    &-secondary{
        background: $secondary;
    }
    &-darkBrown{
        background:$orange-800;
    }
    &-lightBrown{
        background: $orange-600;
    }
}
//Border  
.border{
        border-width: 1px;        
        border-style: solid;
    &-t{
        border-width: 1px 0 0 0;
        border-style: solid;
    }
    &-b{
        border-width: 0 0 1px 0;
        border-style: solid;
    }
}
//Border color
.border{
    &-dark{
        border-color: $dark;
    }
    &-gray-200{
        border-color: $gray-200;
    }
    &-white{
        border-color: $white;
    }
}

//Flex
.d-flex{
    display: flex;
}
.flex{
    &-row{
        flex-direction: row;
    }
    &-row-reverse{
        flex-direction: row-reverse;
    }
    &-column{
        flex-direction: column;
    }
    &-column-reverse{
        flex-direction: column-reverse;
    }
    &-wrap{
        flex-wrap: wrap;
    }
    &-nowrap{
        flex-wrap: nowrap;
    }
}
.justify{
    &-start{
        justify-content: flex-start;
    }
    &-end{
        justify-content: flex-end;
    }
    &-center{
        justify-content: center;
    }
    &-between{
        justify-content: space-between;
    }
    &-around{
        justify-content: space-around;
    }
}
.align{
    &-items-start{
        align-items: flex-start;
    }
    &-items-end{
        align-items: flex-end;
    }
    &-items-center{
        align-items: center;
    }
    &-items-baseline{
        align-items: baseline;
    }
}
